@charset "UTF-8";
:root {
  --color-black: #111;
  --color-white: #fff;
  --color-blue: #3c64ff;
  --color-blue-bg: #82aaff;
  --color-gray: #b5b5b5;
  --color-gray-bg: #f5f5f5;
  --color-red: #ff4664;
  --color-green: #9be55c;
}

@font-face {
  font-family: "PT Root UI";
  font-display: swap;
  src: url("../fonts/PT Root UI-Bold.woff") format("woff"), url("../fonts/PT Root UI-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PT Root UI";
  font-display: swap;
  src: url("../fonts/PT Root UI-Medium.woff") format("woff"), url("../fonts/PT Root UI-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "PT Root UI";
  font-display: swap;
  src: url("../fonts/PT Root UI-Regular.woff") format("woff"), url("../fonts/PT Root UI-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

