html {
  overflow: -moz-scrollbars-none;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: "PT Root UI", sans-serif;
  font-weight: 400;
  font-size: 32px;
  color: var(--color-black);
  background-color: var(--color-white);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

figure {
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  cursor: pointer;
}

span {
  display: block;
}

input,
textarea {
  outline: none;
}

picture {
  font-size: 0;
}

.container {
  /* max-width: 1250px; */
  padding: 0 20px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.section-body {
  margin: 0px auto;
  width: 100%;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  background-color: transparent;
}

.header__container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 20px;
}

.logo {
  z-index: 200;
  display: inline-block;
  width: 120px;
  height: 122px;
}

.cls-1,
.cls-2 {
  fill: var(--color-black);
}

.cls-2 {
  stroke: var(--color-black);
  stroke-miterlimit: 10;
  stroke-width: 0.02px;
}

.nav__list {
  display: flex;
  padding-top: 10px;
}

.nav__item:not(:first-child) {
  margin-left: 43px;
}

.nav__item--first {
  display: none;
  opacity: 0;
}

.nav__link {
  padding-bottom: 5px;
  font-weight: 500;
  font-size: 32px;
  color: var(--color-black);
  transition: color 0.3s ease-in-out;
}

.nav__link:hover {
  color: var(--color-red);
  transition: color 0.3s ease-in-out;
}

.nav__link--order {
  padding: 15px;
  color: var(--color-white);
  background-color: var(--color-blue);
  transition: background-color 0.3s ease-in-out;
}

.nav__link--order:hover {
  color: var(--color-white);
  background-color: var(--color-red);
  transition: background-color 0.3s ease-in-out;
}

.active-menu {
  color: var(--color-gray);
}

.nav__link.active-menu:hover {
  color: var(--color-gray);
}

.menu-open-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  display: none;
  margin-top: 15px;
  border: 1px solid var(--color-gray);
  border-radius: 18px;
  width: 68px;
  height: 24px;
  font-weight: 400;
  font-size: 14px;
  color: var(--color-gray);
  opacity: 0;
  visibility: hidden;
}

.menu-close {
  position: absolute;
  right: 15px;
  top: 20px;
  display: none;
  opacity: 0;
  visibility: hidden;
}

.media-adaptive {
  display: none;
}

.footer {
  padding-bottom: 20px;
  padding-left: 16.5vw;
}

.footer__main {
  display: flex;
  flex-direction: column;
}

.footer__excerpt {
  margin-bottom: 7px;
  font-weight: 400;
  font-size: 20px;
  color: var(--color-gray);
}

.footer__email {
  margin-left: -1px;
}

.footer__order-link {
  margin-left: -3px;
  font-weight: 700;
  letter-spacing: -0.02em;
}

.footer-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 20px;
}

.media {
  padding-top: 25px;
}

.media__item {
  display: flex;
  justify-content: flex-end;
}

.media__link {
  z-index: 200;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  color: var(--color-black);
  transition: color 0.3s ease-in-out;
}

.media__link:hover {
  color: var(--color-red);
  transition: color 0.3s ease-in-out;
}

.copyright {
  margin-top: 55px;
  font-weight: 400;
  font-size: 20px;
}

.thanks-popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.thanks-popup__body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 110px 140px 100px;
  max-width: 960px;
  max-height: 480px;
  background-color: var(--color-white);
}

.thanks-popup__close {
  padding: 0;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 25px;
  top: 20px;
  width: 40px;
  height: 40px;
}

.thanks-popup__close span {
  position: absolute;
  left: 0;
  top: 50%;
  display: block;
  width: 50px;
  height: 1px;
  background-color: var(--color-gray);
  transform: translateY(-50%);
}

.thanks-popup__close span:first-child {
  transform: rotate(45deg);
}

.thanks-popup__close span:last-child {
  transform: rotate(-45deg);
}

.thanks-popup__text {
  margin-bottom: 25px;
  max-width: 600px;
  font-weight: 500;
  font-size: 50px;
  line-height: 60px;
  text-align: center;
}

.thanks-popup__btn {
  padding: 0;
  background-color: transparent;
  border: none;
  width: 130px;
  height: 64px;
  color: var(--color-white);
  background-color: var(--color-blue);
  transition: background-color 0.5s ease-in-out;
}

.thanks-popup__btn:hover {
  background-color: var(--color-red);
  transition: background-color 0.5s ease-in-out;
}

.thanks-popup__text span {
  text-align: center;
}

.thanks-popup-open {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.site-container {
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
}

.wrapper {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  flex: 1 1 auto;
}

.first {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
  padding-bottom: 15px;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  min-height: 450px;
  max-width: 100%;
  background-image: url("../img/background-logo.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.first__main {
