.contacts {
  display: flex;
  align-items: flex-end;
  padding-bottom: 15px;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  min-height: 400px;
}

.contacts__main {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.contacts__title {
  z-index: 100;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 2.60417vw;
  color: var(--color-gray);
}

.contacts__info {
  display: flex;
  flex-direction: column;
  margin-bottom: 65px;
}

.order-link {
  font-size: 6.25vw;
  color: var(--color-blue);
  transition: color 0.3s ease-in-out;
  cursor: pointer;
}

.order-link:hover {
  color: var(--color-red);
  transition: color 0.3s ease-in-out;
}

.contact {
  z-index: 100;
  font-weight: 700;
  line-height: 6.25vw;
  letter-spacing: -0.02em;
}

.phone-number {
  font-size: 6.25vw;
}

.email {
  font-size: 6.25vw;
}

.mobile-media {
