.project__items {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 8px;
  width: 600px;
}

.project__label:not(:last-child) {
  margin-right: 12px;
}

.project__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.project > span {
  display: flex;
  align-items: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.project__item::before {
  content: "";
  display: inline-block;
  border: 1px solid var(--color-gray);
  border-radius: 20px;
  padding: 7px 18px;
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  color: var(--color-gray);
  cursor: pointer;
}

.project__item--logo::before {
  content: "логотип";
}

.project__item--identity::before {
  content: "айдентика";
}

.project__item--landing::before {
  content: "лендинг";
}

.project__item--other::before {
  content: "другое";
}

.project__label > input:checked + span::before {
  border-color: var(--color-blue);
  box-shadow: 0 0 0 2px var(--color-blue);
  color: var(--color-blue);
}

.task__title {
